.testimonials {
  &__item {
    margin-bottom: 48px;

    &:last-child {
      margin-bottom: 0;
    }

    @include onDesktop {
      margin-bottom: 0;
    }
  }
}

.testimonial {
  &__photo-container {
    display: grid;
    justify-content: center;
    align-items: center;

    margin-bottom: 28px;

    @include onDesktop {
      grid-template-columns: repeat(4, 1fr);
      column-gap: 24px;
      margin-bottom: 32px;
    }
  }

  &__link {
    @include onDesktop {
      grid-column: 2 / 4;
    }
  }

  &__photo {
    @include hover(transform, scale(1.1));

    &:hover {
      cursor: pointer;
    }
  }

  &__name {
    margin: 0 0 4px;
    font-weight: 700;
    font-size: 24px;
    line-height: 30px;
    text-align: center;
  }

  &__role {
    margin: 0 0 4px;
    font-size: 14px;
    line-height: 27px;
    text-align: center;
    color: #9fadc2;
  }

  &__about {
    text-align: center;
  }
}

.link {
  text-decoration: none;
  color: $colorBlue;

  @include hover(color, $colorBlueHover);
}
